import React from "react"
import { Link } from "gatsby"
import * as css from "./navigation.module.scss"
import {
  ArrowsAngleExpand,
  ArrowsAngleContract,
  QuestionDiamond,
} from "@styled-icons/bootstrap"
import { Lightbulb as LightOff } from "@styled-icons/fa-regular"
import { Lightbulb as LightOn } from "@styled-icons/fa-solid"

import classNames from "classnames"

export default class Navigation extends React.Component {
  render() {
    const { title, changeWidth, darkMode, dark, wide } = this.props
    //           <Link className={css.link} to="/tags">Tags</Link>

    return (
      <nav className={css.nav}>
        <Link className={css.title} to="/">
          {title}
        </Link>
        <div>
          <Link
            aria-label="Read about me"
            className={classNames(css.toggle, css.link)}
            to="/about"
          >
            <QuestionDiamond />
          </Link>
          <button
            aria-label="Toggle widescreen mode"
            className={classNames(css.toggle, css.wideToggle)}
            onClick={changeWidth}
          >
            {wide ? <ArrowsAngleContract /> : <ArrowsAngleExpand />}
          </button>
          <button
            aria-label="Toggle dark mode"
            className={css.toggle}
            onClick={darkMode}
          >
            {dark ? <LightOff /> : <LightOn />}
          </button>
        </div>
      </nav>
    )
  }
}
